// src/types/index.ts - 

// ====================  ====================

export interface Position {
  x: number;
  y: number;
}

// ====================  ====================

export interface Effect {
  name: string;
  duration: number;
  attack?: number;
  defense?: number;
  speed?: number;
}

// ====================  ====================

export interface Entity extends Position {
  name: string;
  hp: number;
  maxHp: number;
  attack: number;
  defense: number;
  speed: number;
  evasion: number;
  ap: number;
  effects: Effect[];
  stunned: number;
}

export interface Player extends Entity {
  mp: number;
  maxMp: number;
  torch: number;
  maxTorch: number;
  level: number;
  exp: number;
  nextLevelExp: number;
  gold: number;
  critChance: number;
  critDamage: number;
  luck: number;
  hpRegen: number;
  mpRegen: number;
  lifesteal: number;
  thorns: number;
  skillSlots: number;
  skillIds: string[];
  isDashing: boolean;
}

export interface Enemy extends Entity {
  char: string;
  exp: number;
  special: EnemySpecial;
}

export type EnemySpecial = 'none' | 'split' | 'heal' | 'erratic';

// ====================  ====================

export type ItemType = 'portal' | 'gold' | 'potion' | 'oil';

export interface Item extends Position {
  type: ItemType;
}

// ====================  ====================

export enum TileType {
  WALL = 0,
  FLOOR = 1
}

export interface Room {
  x: number;
  y: number;
  w: number;
  h: number;
}

export interface FovTile {
  visible: boolean;
  explored: boolean;
}

// ====================  ====================

export type SkillType = 'damage' | 'heal' | 'buff' | 'control' | 'utility';

export interface SkillDefinition {
  id: string;
  name: string;
  cost: number;
  type: SkillType;
  description: string;
}

export interface Skill extends SkillDefinition {
  effect: SkillEffectFunction;
}

export interface SkillContext {
  player: Player;
  enemies: Enemy[];
  gameMap: TileType[][];
  killEntity: (entity: Player | Enemy) => void;
  addLog: (message: string, type: LogType) => void;
  updateGame: () => void;
  isWalkable: (x: number, y: number) => boolean;
}

export type SkillEffectFunction = (
  caster: Player,
  target: Position | Enemy | null,
  context: SkillContext
) => boolean | 'prompt_direction';

// ====================  ====================

export interface MonsterStats {
  hp: [number, number];      // [, ]
  attack: [number, number];
  defense: [number, number];
  evasion: number;
  exp: [number, number];
  speed: number;
}

export interface MonsterDefinition {
  id: string;
  name: string;
  char: string;
  minFloor: number;
  maxFloor: number;
  stats: MonsterStats;
  special: EnemySpecial;
}

// ====================  ====================

export interface DifficultyMultipliers {
  hp_multiplier: number;
  attack_multiplier: number;
  defense_multiplier: number;
  exp_multiplier: number;
  speed_multiplier: number;
}

// ====================  ====================

export type ShopItemCategory = 'consumable' | 'permanent' | 'special';

export interface ShopItem {
  id: string;
  name: string;
  description: string;
  price: number;
  category: ShopItemCategory;
  effect: (player: Player) => void;
  canBuy?: (player: Player) => boolean;
}

// ====================  ====================

export type LogType = 'player' | 'enemy' | 'system' | 'item' | 'skill';

export interface LogEntry {
  id: number;
  message: string;
  type: LogType;
  timestamp: number;
}

// ====================  ====================

export interface VisualEffect {
  type: 'area' | 'projectile' | 'flash';
  tiles: Position[];
  color: string;
  duration: number;
}

export interface GroundEffect {
  tiles: Position[];
  duration: number;
  effect: (entity: Entity) => void;
  color: string;
}

// ====================  ====================

export type GameScreen = 
  | 'start' 
  | 'charCreation' 
  | 'skillSelection' 
  | 'playing' 
  | 'levelUp' 
  | 'shop'
  | 'gameOver';

export interface GameState {
  screen: GameScreen;
  player: Player | null;
  enemies: Enemy[];
  items: Item[];
  gameMap: TileType[][];
  fovMap: FovTile[][];
  floor: number;
  turnCount: number;
  isPlayerTurn: boolean;
  gameActive: boolean;
  logs: LogEntry[];
  visualEffects: VisualEffect[];
  groundEffects: GroundEffect[];
}

// ====================  ====================

export interface LevelUpOption {
  id: string;
  text: string;
  apply: (player: Player) => void;
}

// ====================  ====================

export interface StatAllocation {
  name: string;
  key: keyof PlayerBaseStats;
  base: number;
  step: number;
  value: number;
}

export interface PlayerBaseStats {
  maxHp: number;
  maxMp: number;
  maxTorch: number;
  attack: number;
  defense: number;
  speed: number;
  critChance: number;
  critDamage: number;
  evasion: number;
  luck: number;
  hpRegen: number;
  mpRegen: number;
  lifesteal: number;
  thorns: number;
  skillSlots: number;
}

// ====================  ====================

export type GameEventType = 
  | 'playerMove'
  | 'playerAttack'
  | 'playerSkill'
  | 'enemyTurn'
  | 'entityDeath'
  | 'levelUp'
  | 'floorChange'
  | 'itemPickup'
  | 'shopOpen';

export interface GameEvent {
  type: GameEventType;
  payload?: unknown;
}
